/**
 * Created by heju on 2017/7/14.
 */
let commonUtil = {
    asyncLoadedScripts: {},
    asyncLoadedScriptsCallbackQueue: {},
    getScriptDomFromUrl : (url)=>{
        let dom;
        if (/.+\.js$/.test(url))
        {
            dom = document.createElement("SCRIPT");
            dom.setAttribute("type", "text/javascript");
            dom.setAttribute("src", url);
        }
        else if(/.+\.css$/.test(url))
        {
            dom = document.createElement('link');
            dom.href = url;
            dom.type = "text/css";
            dom.rel="stylesheet";
        }
        return dom;
    },
    /**
     * scriptcss
     */
    asyncLoadScript: (url, callback) => {
        let $this = commonUtil;
        if ($this.asyncLoadedScripts[url] != undefined)//script
        {
            if (callback && typeof(callback) == "function") {
                if ($this.asyncLoadedScripts[url] == 0)//script
                {
                    if (!$this.asyncLoadedScriptsCallbackQueue[url]) {
                        $this.asyncLoadedScriptsCallbackQueue[url] = [];
                    }
                    $this.asyncLoadedScriptsCallbackQueue[url].push(callback);
                }
                else {
                    callback.apply($this, []);
                }
            }
            return;
        }
        $this.asyncLoadedScripts[url] = 0;
        let scriptDom = $this.getScriptDomFromUrl(url);
        if (scriptDom.readyState) {
            scriptDom.onreadystatechange = function () {
                if (scriptDom.readyState == "loaded" || scriptDom.readyState == "complete") {
                    scriptDom.onreadystatechange = null;
                    $this.asyncLoadedScripts[url] = 1;
                    if (callback && typeof(callback) == "function") {
                        callback.apply($this, []);
                    }
                    if ($this.asyncLoadedScriptsCallbackQueue[url]) {
                        for (let i = 0, j = $this.asyncLoadedScriptsCallbackQueue[url].length; i < j; i++) {
                            $this.asyncLoadedScriptsCallbackQueue[url][i].apply($this, []);
                        }
                        $this.asyncLoadedScriptsCallbackQueue[url] = undefined;
                    }
                }
            }
        }
        else {
            scriptDom.onload = function () {
                $this.asyncLoadedScripts[url] = 1;
                if (callback && typeof(callback) == "function") {
                    callback.apply($this, []);
                }
                if ($this.asyncLoadedScriptsCallbackQueue[url]) {
                    for (let i = 0, j = $this.asyncLoadedScriptsCallbackQueue[url].length; i < j; i++) {
                        $this.asyncLoadedScriptsCallbackQueue[url][i].apply($this, []);
                    }
                    $this.asyncLoadedScriptsCallbackQueue[url] = undefined;
                }
            }
        }
        document.getElementsByTagName('head')[0].appendChild(scriptDom);
    },
    concatList : (list, addList)=>{
        addList.forEach((item)=>{
            list.push(item);
        });
    },
    isEmptyObj : (obj)=>{
        for (let key in obj)
        {
            return false;
        }
        return true;
    },
    /**
     * 
     * @param obj 
     * @param dest 
     * @param override false:desttrue:obj
     * @return 
     */
    copyObject : (obj, dest, override)=>{
        if (override == undefined)
        {
            override = true;
        }
        let result = dest || {};
        for (let key in obj)
        {
            if (!override && result[key])
            {
                continue;
            }
            else
            {
                if (!obj[key] && obj[key] != 0)
                {
                    continue;
                }
                result[key] = obj[key];
            }
        }
        return result;
    },
    copyArray : (obj)=>{
        let result = [];
        for (let i = 0, j = obj.length; i < j; i++)
        {
            if (typeof(obj[i]) == "object")
            {
                result[i] = commonUtil.copyObject(obj[i]);
            }
            else
            {
                result[i] = obj[i];
            }
        }
        return result;
    },
    /**
     * key
     * @param arr 
     * @param key 
     * @param value key
     * @return 
     */
    getArrayInfoByKey : (arr, key, value)=>{
        let item;
        for (let i = 0, j = arr.length; i < j; i++)
        {
            item = arr[i];
            if (item[key] === value)
            {
                return item;
            }
        }
        return undefined;
    },
    /**
     * 
     * @param arr
     * @param object
     */
    inArray : (arr, object)=>{
        if (arr == undefined || object == undefined)
        {
            return false;
        }
        let item;
        for (let i = 0,j = arr.length; i < j; i++)
        {
            item = arr[i];
            if (item === object)
            {
                return true;
            }
        }
        return false;
    }
};
export default commonUtil;


